.frame-child {
  position: relative;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  object-fit: cover;
  z-index: 0;
}
.frame-item {
  position: absolute;
  margin: 0 !important;
  top: 53px;
  left: 53px;
  border-radius: 50%;
  background-color: var(--color-mediumslateblue);
  border: 1.5px solid var(--white);
  box-sizing: border-box;
  width: 27px;
  height: 27px;
  z-index: 1;
}
.mingcutequote-right-fill-icon {
  position: absolute;
  margin: 0 !important;
  top: 59px;
  left: 59px;
  width: 16px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 2;
}
.ellipse-parent,
.lorem-ipsum-dolor {
  position: relative;
  display: flex;
  align-items: center;
}
.ellipse-parent {
  flex-direction: row;
  justify-content: flex-start;
  gap: 17.78px;
}
.lorem-ipsum-dolor {
  letter-spacing: 0.01em;
  line-height: 24px;
  justify-content: center;
  width: 270px;
}
.frame-parent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.frame-inner {
  position: relative;
  border-radius: 50%;
  background-color: var(--color-mediumslateblue);
  border: 1.5px solid var(--white);
  box-sizing: border-box;
  width: 12px;
  height: 12px;
}
.allison-adam,
.founder-ceo {
  position: relative;
  letter-spacing: 0.04em;
  line-height: 24px;
}
.allison-adam {
  font-weight: 600;
}
.founder-ceo {
  font-size: var(--small-text-regular-size);
  color: var(--gray-neutral);
}
.allison-adam-parent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4px;
}
.ellipse-group,
.testimonial-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  font-size: var(--body-text-semibold-18-size);
}
.testimonial-card {
  border-radius: var(--br-xs);
  background-color: var(--white);
  padding: var(--padding-21xl) var(--padding-27xl);
  gap: var(--gap-5xl);
  text-align: center;
  font-size: var(--body-text-regular-16-size);
  color: var(--black-lighter);
  font-family: var(--small-text-regular);
}
