.frame-icon {
  position: absolute;
  top: 8px;
  left: 9px;
  width: 21.75px;
  height: 24px;
  overflow: hidden;
  object-fit: cover;
}
.social-card-inner {
  position: relative;
  border-radius: var(--br-5xs);
  background-color: var(--light-blue);
  width: 40px;
  height: 40px;
  overflow: hidden;
  flex-shrink: 0;
}
.quality-job {
  position: relative;
  letter-spacing: 0.04em;
  line-height: 24px;
  font-weight: 600;
}
.social-card {
  border-radius: var(--br-5xs);
  background-color: var(--white);
  box-shadow: var(--medium-shadow);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-21xl) var(--padding-base)
    var(--padding-5xl);
  gap: var(--gap-base);
  text-align: left;
  font-size: var(--body-text-regular-16-size);
  color: var(--black-lighter);
  font-family: var(--small-text-regular);
}
