.line-div {
  position: relative;
  border-top: 1.3px solid var(--white);
  box-sizing: border-box;
  width: 180.3px;
  height: 1.3px;
}
.hour-ago {
  position: relative;
  letter-spacing: 0.04em;
  line-height: 24px;
}
.line-parent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-mini);
}
.group-icon1,
.heroiconsusers {
  position: relative;
  width: 43.47px;
  height: 43.47px;
}
.heroiconsusers {
  width: 16px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
}
.heroiconsusers-parent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-7xs);
}
.behance-parent,
.group-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.group-group {
  flex-direction: row;
  align-items: center;
  gap: var(--gap-3xs);
}
.jobs {
  position: absolute;
  top: -1px;
  left: 6px;
  letter-spacing: 0.04em;
  line-height: 24px;
}
.jobs-wrapper {
  position: relative;
  border-radius: var(--br-5xl);
  background-color: var(--color-lightgoldenrodyellow);
  width: 64px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
  color: var(--black);
}
.frame-parent13 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.card-description {
  position: absolute;
  right: 22px;
  bottom: 24px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
  text-align: left;
  font-size: var(--small-text-regular-size);
  color: var(--white);
  font-family: var(--font-sk-modernist);
}
