@import url("https://fonts.googleapis.com/css2?family=Proxima+Nova:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Sk-Modernist:wght@400;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --small-text-regular: "Proxima Nova";
  --font-sk-modernist: Sk-Modernist;

  /* font sizes */
  --small-text-regular-size: 12px;
  --body-text-semibold-18-size: 18px;
  --body-text-regular-16-size: 16px;
  --font-size-5xl: 24px;
  --font-size-10xl-3: 29.3px;
  --font-size-17xl: 36px;
  --font-size-29xl: 48px;

  /* Colors */
  --white: #fff;
  --gray-neutral: #6e7175;
  --color-dimgray-100: #4f4f4f;
  --color-dimgray-200: #4d4d4d;
  --black-lighter: #24272d;
  --color-gray-100: #252525;
  --black: #131517;
  --color-mediumslateblue: #966af5;
  --light-blue: #e4eefc;
  --primpary: #0146b1;
  --light-gray: #d6dae0;
  --color-gainsboro-100: #d5dbdb;
  --color-lightgoldenrodyellow: #dfffd0;
  --color-orange: #fca326;
  --secondary: #00a79d;
  --blue-lighter: #f3f7fa;
  --color-peachpuff: #fdc0ae;
  --color-black: #000;
  --color-plum: #fea7d5;
  --secondary-light: #5afef4;
  --color-silver: #a7c3b0;

  /* Gaps */
  --gap-5xl: 24px;
  --gap-5xs: 8px;
  --gap-base: 16px;
  --gap-mid-8: 17.8px;
  --gap-78xl: 97px;
  --gap-13xl: 32px;
  --gap-108xl: 127px;
  --gap-21xl: 40px;
  --gap-10xs-2: 2.2px;
  --gap-44xl: 63px;
  --gap-29xl: 48px;
  --gap-smi: 13px;
  --gap-45xl: 64px;
  --gap-3xs: 10px;
  --gap-31xl: 50px;
  --gap-7xs: 6px;
  --gap-mini: 15px;
  --gap-xs: 12px;
  --gap-4xl: 23px;
  --gap-8xs: 5px;
  --gap-xl: 20px;
  --gap-30xl: 49px;

  /* Paddings */
  --padding-21xl: 40px;
  --padding-27xl: 46px;
  --padding-base: 16px;
  --padding-5xl: 24px;
  --padding-xs: 12px;
  --padding-77xl: 96px;
  --padding-141xl: 160px;
  --padding-125xl: 144px;
  --padding-29xl: 48px;
  --padding-61xl: 80px;
  --padding-5xs: 8px;
  --padding-13xl: 32px;

  /* Border radiuses */
  --br-xs: 12px;
  --br-5xs: 8px;
  --br-5xl: 24px;
  --br-base: 16px;
  --br-12xs-8: 0.8px;
  --br-4xl: 23px;
  --br-9xs: 4px;
  --br-41xl: 60px;

  /* Effects */
  --medium-shadow: 6px 6px 14px 3px rgba(19, 21, 23, 0.06);
  --card-shadow: 5px 9px 20px 3px rgba(19, 21, 23, 0.1);
}
